.funnyvideo {
  height: 86vh;
  overflow: auto;
  width: 100%;
  padding-top: 15px;
  .box {
    float: left;
    list-style: none;
    width: 49%;
  }
  .box1 {
    margin-right: 2%;
  }
  li {
    color: #000;
    position: relative;
    font-size: 0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    margin-bottom: 3vw;
    .videoimg {
      width: 100%;
      height: auto;
      margin-bottom: 5vw;
      border-radius: 3vw;
    }
    .headerimg {
      z-index: 999;
      width: 8vw;
      height: 8vw;
      position: absolute;
      left: 2vw;
      bottom: 0;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .time{
      z-index: 999;
      position: absolute;
      right: 2vw;
      bottom: 1vw;
      font-size: 2vw;
      line-height: 4vw;
      img{
        height: 4vw;
        width: 4vw;
        vertical-align: bottom;
        margin-right: 1vw;
      }
    }
  }
}
::-webkit-scrollbar {
  display: none;
}
