.download{
  font-size: 10vw;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  z-index: 999;
  left: 0;
  top: 0;
  text-align: center;
  line-height: 100vh;
  color: green;
}

.tab {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  font-size: 4vw !important;
}
.navtitle {
  font-size: 4vw !important;
  line-height: 100% !important;
}
.am-tabs-default-bar-tab {
  height: auto !important;
  letter-spacing: 3px;
}
.am-tabs-default-bar-tab-active {
  color: green !important;
  font-weight: bolder;
}
.am-tabs-default-bar-underline {
  border-color: green;
  border-width: 0.3vw;
  background-color: green;
}

.navimg {
  width: 9vw;
  height: auto;
}
.logo {
  width: 100%;
  font-size: 6vw;
  color: green;
  height: 12vw;
  line-height: 12vw;
  .other {
    float: right;
    height: 10vw;
    line-height: 10vw;
    span {
      margin-left: 3vw;
    }
    .seach {
      vertical-align: middle;
      display: inline-block;
      height: 7vw;
      width: 7vw;
      font-size: 0;
      img {
        width: 100%;
        height: auto;
      }
    }
    .app {
      vertical-align: middle;
      font-size: 4vw;
      color: #fff;
      background-color: green;
      display: inline-block;
      border-radius: 1vw;
      height: 6vw;
      line-height: 6vw;
      padding: 0.3vw 1vw;
    }
  }
}
