.video {
  width: 100vw;
  height: 100vh;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #fff;
  z-index: 998;
  overflow: auto;
  video {
    width: 100%;
  }
  .videoBox {
    height: 93vh;
    overflow: auto;
  }
  .navbar {
    position: relative;
    z-index: 999;
    width: 100%;
    height: 7vh;
    .am-navbar-title {
      font-size: 4vw;
    }
    .am-icon-md {
      width: 7vw;
      height: 7vw;
      color: green;
    }
    .userimg {
      position: absolute;
      right: 3vw;
      top: 1vh;
      display: inline-block;
      width: 5vh;
      height: 5vh;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  .content {
    font-size: 4vw;
    padding: 0 3vw;
    .toApp {
      height: 9.6vw;
      width: 93.6vw;
      line-height: 9.6vw;
      background: green;
      border: none;
      border-radius: 4.8vw;
      color: #fff;
      font-size: 3.73333vw;
      text-align: center;
      margin: 4vw auto 4vw;
    }
    .hot {
      background-color: rgba($color: #ccc, $alpha: 0.5);
      color: green;
      border-radius: 3vw;
      font-size: 3vw;
      padding: 1vw;
      text-align: center;
      .iconfont {
        font-size: 3vw;
      }
    }
    .title_text {
      margin-left: 3vw;
      letter-spacing: 2px;
      line-height: 6vw;
    }
    .detail {
      margin: 3vw 0;
      font-size: 3vw;
      .name {
        .up {
          border: 0.3vw solid #ccc;
          border-radius: 1vw;
          margin-right: 2vw;
        }
      }
    }
    .other {
      vertical-align: bottom;
      font-size: 3vw;
      border-bottom: 0.3vw solid #ccc;
      padding-bottom: 3vw;
      span {
        font-size: 3vw;
        margin-left: 3vw;
      }
      .icon-zan {
        margin-left: 0;
      }
      .time {
        float: right;
      }
    }
    .talk {
      margin: 3vw 0;
      .talkimg {
        width: 10vw;
        height: 10vw;
        overflow: hidden;
        border-radius: 50%;
        float: left;
        margin-right: 10vw;
        img {
          width: 100%;
          height: auto;
        }
      }
      .talktxt{
          float: left;
          width: 70vw;
          font-size: 4vw;
          letter-spacing: 2px;
          line-height: 6vw;
          .name{
              margin-bottom: 3vw;
          }
      }
    }
  }
}
