.funnytext {
  height: 86vh;
  overflow: auto;
  width: 100%;
  .lists {
    list-style: none;
    .list {
      .content {
        letter-spacing: 2px;
        font-size: 4vw;
        line-height: 6vw;
      }
      .am-card-header-content {
        letter-spacing: 2px;
        font-size: 5vw;
        line-height: 7vw;
      }
      .am-card:not(.am-card-full)::before{
          border: .5vw solid green !important;
      }
      .am-card-body::before{
          background-color: green;
          height: .5vw !important;
      }
      .time{
        font-size: 3vw;
        margin: 1vw;
      }
    }
  }
}
